import { Component, OnInit, ViewChild } from '@angular/core';
import { AppLoaderService } from '../../core/components/app-loader/app-loader.service';
import { SignUpService } from '../sign-up.service';
import { Router } from '@angular/router';
import { SignInService } from '../../sign-in/sign-in.service';
import { MassageMasterService } from '../../massage-master.service';

@Component({
  selector: 'app-confirm-email',
  templateUrl: './confirm-email.component.html',
  styleUrls: ['./confirm-email.component.scss']
})
export class ConfirmEmailComponent implements OnInit {

  public confirmEmailJson: any = {};
  public emailId: string = '';
  public otpClass: Array<string> = [];
  public otpTime: any = '01:00';
  public seconds = 59;
  public showModule: boolean = false;
  public congratulationsJson: any = {};
  public isCongrats: boolean = false;
  public reference: any = {};

  otp: string = '';
  showOtpComponent = true;

  @ViewChild('ngOtpInput', { static: true }) ngOtpInput: any;
  config = {
    allowNumbersOnly: true,
    length: 5,
    isPasswordInput: false,
    disableAutoFocus: false,
    placeholder: '',
    inputStyles: {
      'width': '40px',
      'height': '40px'
    }
  };

  constructor(private appLoaderService: AppLoaderService,
    private massageMasterService: MassageMasterService,
    private signUpService: SignUpService, private signInService: SignInService,
    private _router: Router) { }

  public onOtpChange(otp): void {
    this.otp = otp;
  }

  ngOnInit() {
    this.appLoaderService.showLoader();
    this.signInService.currentId.subscribe(id => {
      this.appLoaderService.hideLoader();
      if (id !== null) {
        this.emailId = id;
        this.showModule = true;
        this.getPasswordRecovery();
      } else {
        this._router.navigate(['authentication/signIn']);
      }
    });

    this.signInService.currentReference.subscribe(reference => {
      this.appLoaderService.hideLoader();
      if (reference) {
        this.reference = reference;
      }
    })
  }

  private getPasswordRecovery(): void {
    this.confirmEmailJson = this.massageMasterService.getMessageJsonData();
    this.config.length = this.confirmEmailJson.formFields.otp.otpLength;
    this.showModule = true;
    this.countDown();
  }

  private countDown(): void {
    setInterval(() => {
      if (this.seconds == 0) {
        return;
      }
      this.otpTime = this.seconds--;
    }, 1000);
  }

  public submit(): void {
    const obj = {
      otp: this.otp,
      emailId: this.emailId,
      reference: this.reference
    };
    this.appLoaderService.showLoader();
    this.signUpService.confirmEmailApi(obj).subscribe(result => {
      console.log('result', result);
      this.appLoaderService.hideLoader();
      const data = result['data'];
      if (data.status.toLowerCase() === 'success') {
        localStorage.removeItem('emailId');
        this.congratsData();
      } else {
        this.massageMasterService.showError(this.massageMasterService.getErrors().default);
      }
    }, (err) => {
      console.log('error', err);
      this.appLoaderService.hideLoader();
      const errorObj = JSON.parse(err.completeError.response);
      const msg = errorObj.message ? errorObj.message : this.massageMasterService.getErrors().default;
      this.massageMasterService.showError(msg)
    });
  }

  public reSendOtp(): void {
    this.appLoaderService.showLoader();
    this.showOtpComponent = false;
    this.otp = '';
    setTimeout(() => {
      this.showOtpComponent = true;
    }, 0);

    this.signInService.generateOtp(this.emailId, 'signup').subscribe(result => {
      this.appLoaderService.hideLoader();
      console.log('result', result);
      const data = result['data'];
      if (data.status.toLowerCase() === 'success') {
        this.reference = data.response.response.reference;
      } else {
        this.massageMasterService.showError(this.massageMasterService.getErrors().default);
      }
    }, (err) => {
      console.log('error', err);
      this.appLoaderService.hideLoader();
      const errorObj = JSON.parse(err.completeError.response);
      const msg = errorObj.message ? errorObj.message : this.massageMasterService.getErrors().default;
      this.massageMasterService.showError(msg)
    });

  }

  public congratsData(): void {
    this.isCongrats = true;
  }

  public login(): void {
    this._router.navigate(['authentication/signIn']);
  }

  public notUser(): void {
    this._router.navigate(['authentication/signIn']);
  }
}
